<template>
  <div>
    <b-row>
      <b-col>
        <div class="d-inline-flex">
          <input type="text" v-model="category" class="form-control">
          <button @click="addCategory" class="btn btn-primary btn-sm ml-2">ADD</button>
        </div>
      </b-col>
    </b-row>
    <b-row class="mt-3">
      <b-col offset-md="4">
        <table style="width:50%">
          <tr>
            <th></th>
            <th></th>
          </tr>
          <tr v-for="(category, index) in categories" :key="index">
            <td>{{category}}</td>
            <td>
              <button @click="deleteCategory(index)" class="btn btn-danger btn-sm">DELETE</button>
            </td>
          </tr>
        </table>
      </b-col>
    </b-row>
  </div>
</template>
<script>
export default {
  data () {
    return {
      category: '',
      categories: []
    }
  },
  mounted () {
    this.categories = JSON.parse(localStorage.getItem('Category')) ? JSON.parse(localStorage.getItem('Category')) : []
  },
  methods: {
    addCategory () {
      this.categories.push(this.category)
      localStorage.setItem('Category', JSON.stringify(this.categories))
      this.category = ''
    },
    deleteCategory (index) {
      this.categories.splice(index, 1)
      localStorage.setItem('Category', JSON.stringify(this.categories))
    }
  }
}
</script>
